Vue.filter('formatDate', function (str) {
  if (!str) return ''
  var date = new Date(str)
  //getTime()1970.1.1
  var time = new Date().getTime() - date.getTime() //- =  = 
  if (time < 0) {
    return ''
  } else if ((time / 1000 < 30)) {
    return ''
  } else if (time / 1000 < 60) {
    return parseInt((time / 1000)) + ''//1000ms 1s
  } else if ((time / 60000) < 60) {
    return parseInt((time / 60000)) + ''//60*1000 1min
  } else if ((time / 3600000) < 24) {
    return parseInt(time / 3600000) + ''//60*60*1000 1h
  } else if ((time / 86400000) < 31) {
    return parseInt(time / 86400000) + ''//60*60*1000*24
  } else if ((time / 2592000000) < 12) {
    return parseInt(time / 2592000000) + ''//60*60*1000*24*30
  } else {
    return parseInt(time / 31536000000) + ''
  }
}
)
