<template>
  <div class="pagination">
    <!-- event -->
    <button @click="changePage"></button>
    <button @click="changePage"></button>
    <button v-if="this.currentPage>=5" class="pagebtn">......</button>
    <!--  -->
    <!-- page -->
    <button
      v-for="page in pages"
      @click="changePage(page)"
      :class="[{currentPage: page == currentPage},'pagebtn']"
    >{{page}}</button>
    <button @click="changePage"></button>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: "Pagination",
  data() {
    return {
      pages: [1, 2, 3, 4, 5, "..."],
      currentPage: 1 ,//
    };
  },//123456...
  methods: {
    changePage(page) {
      if(typeof page != 'number'){
        switch(page.target.innerText){
          case '':
          $('.currentPage').ready().prev().click()
          break
          case '':
          $('.currentPage').ready().next().click()
          break
          case '':
          this.pages = [1, 2, 3, 4, 5, "......"]
          this.changePage(1)
          break
          default:
          break
        }
        return
      }
      this.currentPage = page;
      if(page==this.pages[4]){
        //
        this.pages.shift()
        //541
        this.pages.splice(4,0,this.pages[3]+1)
      }else if(page==this.pages[0] && page !==1){
      // 101
       var lastPage = this.pages[0]-1
       this.pages.unshift(lastPage)
       this.pages.splice(5,1)
      }
      this.$emit('handel',this.currentPage)
      console.log(this.currentPage)
    }
  }
};
</script>

<style scoped>
.pagination {
  margin-top: 15px;
  margin-bottom: 20px;
  background: rgba(0,0,0,0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid #888888;
}

button {
  border: 1px solid #ccc;
  color: #bbb;
  border-radius: 6px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 3px 6px;
  height: 29px;
  background: none;
  background-color: rgba(0,0,0,0.1);
  margin: 0 1%;
  font-family: pika;
}

.pagebtn {
  /* position: relative; */
  bottom: 1px;
  width: 40px;
  /* margin: 0 4px; */
}

.currentPage {
  color: black;
  background: #3a3f41 url(../assets/note-bg.jpg);
  font-weight: bold;
}
</style>



